import {useState, useEffect} from 'react'
import pic from '../Media/Wow.jpg'
import pic2 from '../Media/Screenshot_200523_000.jpg'
import pic3 from '../Media/47.png'
import pic4 from '../Media/45.png'
import pic5 from '../Media/46.png'

const ImageSlider = () => {
  const [img, setimg] = useState([pic, pic2, pic3, pic4, pic5]);
  const [idx, setidx] = useState(0);

  function handleNext(){
    setidx(idx + 1);
  }
  
  return (
    <div>
      {console.log(idx)}
      <button onClick={() => setidx(idx < 1 ? img.length - 1 : idx - 1)}>
        previous
      </button>
       <img style={{
         width: 100,
         height: 100
       }}src={img[idx]} />
       <button onClick={() => setidx(idx > img.length - 2 ? 0 : idx + 1)}>
         next
       </button>
    </div>
  )
}

export default ImageSlider
